import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
import { UserProfileService } from '../../services/userProfileService/user-profile.service';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
// import { TranslateService } from '@ngx-translate/core';
// import { LanguageManagementService } from '../../../shared/services/languageManagementService/languageManagement.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  pushRightClass: string = 'push-right';
  userData: any;
  userName: String;
  innerHeight: any;
  innerWidth: any;
  currentPassword: any;
  enterPassword: any;
  reenterPassword: any;
  message: any;
  errorMessage: any;
  Id: any;
  languages: any;
  language: any;
  languageCode: any;
  labels: any;
  code: any;
  lockImagePath = './assets/lock.png'
  constructor(private LocalStorageService: LocalStorageService, private modalService: NgbModal,
    private UserProfileService: UserProfileService, public router: Router) {
    this.labels = this.LocalStorageService.retrieve("SelectedLanguage");
    if (!this.labels) {
      this.code = this.LocalStorageService.retrieve("CurrentLanguage");
      if (this.code) {
        this.code = 'en';
      }
    }
    this.router.events.subscribe((val) => {
      this.toggleSidebar();

    });
  }

  ngOnInit() {
    this.userData = this.LocalStorageService.retrieve("userData");
    this.UserProfileService.getUserDetails().then(
      success => {
        if (success.status === 200) {
          this.userName = success.data[0].FirstName;
          this.Id = success.data[0].Id;
          this.language = success.data[0].Language;
          this.languageCode = success.data[0].LanguageCode;
          this.LocalStorageService.store("CurrentLanguage", this.languageCode);
        }
      });

  }
  resetModel() {
    this.errorMessage = "";
    this.message = "";
    this.currentPassword = "";
    this.enterPassword = "";
    this.reenterPassword = "";
  }
  isToggled(): boolean {
    const dom: Element = document.querySelector('body');
    return dom.classList.contains(this.pushRightClass);
  }

  toggleSidebar() {
    const dom: any = document.querySelector('body');
    dom.classList.toggle(this.pushRightClass);
  }

  rltAndLtr() {
    const dom: any = document.querySelector('body');
    dom.classList.toggle('rtl');
  }

  onLoggedout() {
    this.LocalStorageService.clear();
    this.router.navigate(['/']);
    window.location.reload();
  }
  // onLoggedout() {
  //   localStorage.removeItem('isLoggedin');
  //   this.LocalStorageService.clear();
  //   this.router.navigate(['/login']);
  //   window.location.reload();
  // }

  updateLanguage(l) {
    var params = {
      "LanguageId": l.Id,
      "Id": this.Id
    }
    this.UserProfileService.changeLanguage(params).then(
      success => {
        if (success.status === 200) {
          this.message = success.message;
          this.LocalStorageService.store("CurrentLanguage", l.Code);
        } else {
          this.errorMessage = success.message;
        }
      },
      error => {
        console.log(error)
      });
  }
  save() {
    var params = {
      Id: this.Id,
      Password: btoa(this.currentPassword),
      NewPassword: btoa(this.enterPassword)
    };
    this.message = null;
    this.errorMessage = null;
    this.UserProfileService.changePassword(params).then(
      success => {
        if (success.status === 200) {
          this.message = success.message;
          document.getElementById('closePopup').click();
        } else {
          this.errorMessage = success.message;
        }
      },
      error => {
        console.log(error)
      });
  }
  open(modal) {
    this.LocalStorageService.store("footer", false);
    this.reenterPassword = '';
    this.enterPassword = '';
    this.currentPassword = '';
    this.message = '';
    this.errorMessage = '';
    this.modalService.open(modal).result.then((result) => {
    }, (reason) => { });
  }
  ngAfterViewChecked() {
    var mainDiv = document.getElementsByClassName("modal");
    if (mainDiv[0]) {
      this.innerHeight = window.innerHeight
        || document.documentElement.clientHeight
        || document.body.clientHeight;
      this.innerWidth = window.innerWidth
        || document.documentElement.clientWidth
        || document.body.clientWidth;
      var modal = document.getElementsByClassName("modal-content");
      if (modal[0]) {
        this.offset(modal[0]);
      }
    }

  }
  offset(el) {
    if ((this.innerHeight - el.offsetHeight) > 0) {
      el.style.top = (this.innerHeight - el.offsetHeight) / 2 + "px";
    } else {
      el.style.top = 0 + "px";
    }
  }

}