import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
// import { LanguageManagementService } from '../../../shared/services/languageManagementService/languageManagement.service';

// environment settings
import { environment } from '../../../environments/environment';

@Component({
    selector: 'app-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent {
    isActive = false;
    showMenu = '';
    userData: any;
    superAdmin = environment.SuperAdmin;
    admin = environment.Admin;
    labels: any;
    code: any;

    constructor(private LocalStorageService: LocalStorageService, public router: Router) {

        this.labels = this.LocalStorageService.retrieve("SelectedLanguage");
        if (!this.labels) {
            this.code = this.LocalStorageService.retrieve("CurrentLanguage");
            if (this.code) {
                this.code = 'en';
            }
        }
    }
    eventCalled() {
        this.isActive = !this.isActive;
    }
    addExpandClass(element: any) {
        if (element === this.showMenu) {
            this.showMenu = '0';
        } else {
            this.showMenu = element;
        }
    }
    ngOnInit() {
        this.userData = this.LocalStorageService.retrieve('userData');

    }
}
