import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoaderService } from '../services/loader.service';
import { UserProfileService } from '../services/userProfileService/user-profile.service';
import { LocalStorage, SessionStorage, LocalStorageService } from 'ngx-webstorage';
import { environment } from '../../environments/environment';

@Component({
    selector: 'app-layout',
    templateUrl: './layout.component.html',
    styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {
    objLoaderStatus: boolean;
    userData: any;
    superAdmin = environment.SuperAdmin;

    constructor(public router: Router, private loaderService: LoaderService, private LocalStorageService: LocalStorageService, 
        private UserProfileService: UserProfileService) {
        this.objLoaderStatus = false;
    }

    ngOnInit() {
        this.userData = this.LocalStorageService.retrieve('userData');
        this.UserProfileService.fetchFeatures(this.userData.userId).then(
            success => {
                if (success.data) {
                    for (var item of success.data) {
                        if (item.featureName == "Register Plate Save" && item.access == true) {
                            this.LocalStorageService.store("RegisterPlateFeature", true);
                        }
                    }
                }
            })
        if (this.userData) {
            // this.router.navigate(['/dashboard']);

            this.loaderService.loaderStatus.subscribe((val: boolean) => {
                setTimeout(() => this.objLoaderStatus = val, 0)
            });
        } else {
            this.router.navigate(['/login']);
        }
    }

}
